.table {
    margin-top: 20px;
    overflow-y: hidden;
    height: 400px;
    color: #6a5858;
    flex: 1;

}

.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
    border-color:#CC1034;
}

.table tr {
    display: flex;
    justify-content: space-between;
}

.table td {
    padding: 0.5rem;
}


.table:hover{
    overflow: auto;
}